* {
  outline: none;
}

.navbar {
  width: auto;
  text-align: center;
  background: $white;
  float: left;
  height: 100vh;
  z-index: 999;
  .inner {
    display: flex;
    flex-direction: column;
    margin: 30px 40px 100px;
    position: relative;
    z-index: 999;

    @media only screen and (max-width: 950px) {
      margin: 30px 30px 100px;
    }
  }
  &__content {
    position: fixed;
    top: 0;
    left: 0;
    background: $white;
    padding: 40px 25px 0;

    @media only screen and (max-width: 950px) {
      padding: 40px 13px 0;
    }
    @media only screen and (max-width: 450px) {
      padding: 30px 13px 0;
    }
    box-shadow: 1px 1px 10px darken($color: $silver, $amount: 5);
    height: 100vh;
    // transform: translate(20px, -20px);
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    align-items: center;
    z-index: 999;
    overflow-y: auto;
    max-height: 100vh !important;

    -ms-overflow-style: none;
    /* Internet Explorer 10+ */
    scrollbar-width: none;
    /* Firefox */

    overflow-x: hidden;

    &::-webkit-scrollbar {
      display: none;
      /* Safari and Chrome */
    }
    &_item {
      cursor: pointer;
      margin-bottom: 55px;
      path {
        transition: all 0.3s ease;
      }
      &:hover path {
        fill: #0023ff;
      }
      &:first-child {
        margin-bottom: 100px;
        @media only screen and (max-width: 450px) {
          margin-bottom: 55px;
        }

        @media only screen and (max-height: 600px) {
          margin-bottom: 55px;
        }
      }
      &:last-child {
        margin-top: 30vh;
        margin-bottom: 75px !important;
        @media only screen and (max-height: 766px) {
          margin-top: 20vh;
        }
        @media only screen and (max-height: 650px) {
          margin-top: 10vh;
        }
        @media only screen and (max-height: 600px) {
          margin-top: 0px !important;
        }
      }
    }
  }
}

.searchWrap,
.searchWrap-cart {
  width: 100vw;
  height: 100vh;
  // background-color: rgb(255, 255, 255);
  background-image: linear-gradient(
    90deg,
    rgb(255, 255, 255) 30%,
    rgb(218, 218, 218) 100%
  );
  @media only screen and (max-width: 650px) {
    background: white !important;
  }
  position: fixed;
  z-index: 99;
  left: -200%;
  top: 0;
  transition: all 0.5s ease;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-left: 90px;
  padding-left: 100px;
  @media only screen and (max-width: 1600px) {
    align-items: flex-start;
  }
  @media only screen and (max-width: 950px) {
    padding-left: 50px;
    margin-left: 61px;
  }
  @media only screen and (max-width: 650px) {
    margin: 0 auto !important;
  }
  @media only screen and (max-width: 550px) {
    margin-left: 31px;
  }
  @media only screen and (max-width: 500px) {
    margin-left: 21px;
  }
  @media only screen and (max-width: 450px) {
    margin-left: 11px;
  }
}

.active {
  transition: all 0.5s ease;
  left: 0;
}

.searchText {
  margin-top: 20px;
  color: rgb(202, 202, 202);
  max-width: 65%;
  font-weight: 100;
  @media only screen and (max-width: 850px) {
    display: none;
  }
  &-big {
    color: rgb(39, 39, 39);
    font-size: 60px;
    margin-top: 30px;
    font-weight: 100;
    margin-left: 40px;
    @media only screen and (max-width: 900px) {
      font-size: 40px;
    }
  }
}

.input {
  font-size: 6vw;
  border: none;
  border-bottom: 2px solid #777777;
  transition: all 0.3s ease;
  @media only screen and (max-width: 600px) {
    border-width: 1px;
  }
  @media only screen and (min-width: 1000px) {
    border-width: 3px;
  }
  @media only screen and (max-width: 460px) {
    max-width: 250px;
  }
  &:hover {
    border-color: black;
  }
  padding: 5px 5px 0;
  color: #363636;
  outline: none;
  font-weight: 100;
  max-width: 900px;
  background-color: transparent;
  // text-align: center;
  text-transform: uppercase;
  @media only screen and (min-width: 2000px) {
    font-size: 120px;
  }
  @media only screen and (max-width: 650px) {
    font-size: 30px;
  }
  @media only screen and (max-width: 400px) {
    max-width: 210px;
  }
  @media only screen and (max-width: 350px) {
    max-width: 180px;
  }
}

.input::placeholder {
  color: rgb(223, 223, 223);
}

.searchForm {
  position: relative;
  margin-top: 40px;
  @media only screen and (min-width: 2000px) {
    margin-top: 20px;
  }
  @media only screen and (max-width: 650px) {
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
    right: 17px;
  }
}

.searchClose {
  background-color: transparent;
  border: none;
  font-size: 7vw;
  position: relative;
  top: 15%;
  font-weight: 100;
  color: #777777;
  cursor: pointer;
  transition: color 0.3s ease;
  &:hover {
    color: rgb(54, 54, 54);
  }
  @media only screen and (min-width: 2000px) {
    font-size: 150px;
  }
  @media only screen and (max-width: 650px) {
    font-size: 40px;
  }
  &-small {
    font-size: 45px;
    position: absolute;
    top: 50%;
    right: 1%;
    transform: translate(0, -50%);
    margin-left: 30px;
    margin-right: 7px;
    @media only screen and (max-width: 900px) {
      font-size: 40px;
    }
    @media only screen and (max-width: 650px) {
      top: 30px;
      right: 10px;
      font-size: 44px;

      margin: 0;
    }
    @media only screen and (min-width: 2000px) {
      font-size: 50px;
    }
  }
}

.searchItems {
  margin-top: 0px;
  display: flex;
  max-width: 940px;
  width: auto;
  height: 100vh;
  overflow: auto;
  padding-bottom: 50px;

  @include row-flex();
  @include md(justify-content, start);
  @media only screen and (max-width: 1400px) {
    max-width: 700px;
  }
  @media only screen and (max-width: 1000px) {
    max-width: 600px;
  }

  @media only screen and (max-width: 650px) {
    max-width: 100vw;
    justify-content: center;

    width: 100vw;
  }
  -ms-overflow-style: none;
  /* Internet Explorer 10+ */
  scrollbar-width: none;
  /* Firefox */
  overflow-x: hidden;
}

.searchItems::-webkit-scrollbar {
  display: none;
  /* Safari and Chrome */
}

.searchItem,
.searchItem-cart {
  margin-top: 30px;
  margin-left: 30px;
  background-color: rgba(255, 255, 255, 0.788);
  //  border: 2px solid #e9e9e9;

  padding: 15px 25px;
  display: flex;
  align-items: center;
  position: relative;
  max-height: 300px;
  transition: all 0.3s ease;
  min-height: 300px;

  border-top-right-radius: 15px;
  //  width: 100vw !important;
  max-width: 910px;
  width: 100%;

  @media only screen and (min-width: 650px) {
    width: 100vw !important;
  }
  @media only screen and (max-width: 900px) {
    max-width: 510px !important;
  }

  @include col();
  @include size(12);
  @include size-lg(12);
  @include size-md(12);
  @include size-xs(10);
  border-bottom: 2px solid rgba(128, 128, 128, 0);
  cursor: pointer;

  &:hover {
    border-bottom: 2px solid #0022ffda;
    // background-color: rgba(255, 255, 255, 0.459);
  }
  @media only screen and (max-width: 650px) {
    transform: scale(0.9);

    min-height: 300px !important;
    height: auto !important;
    max-height: 400px;
    width: 100vw !important;
    max-width: 300px !important;
    margin-bottom: auto;
    margin-left: 11px;
    justify-content: center;
  }
  @media only screen and (max-width: 450px) {
    transform: scale(0.8);
    width: 100vw !important;
    max-width: 280px !important;
  }
  .img {
    max-width: 200px;
    @media only screen and (max-width: 650px) {
      margin-bottom: 20px;
    }
  }
  .text {
    display: flex;
  }
  .searchtitle {
    font-size: 35px;
    color: rgb(90, 90, 90);
    margin-left: 10px;
    font-weight: 100;

    &-1 {
      &:after {
        color: $blue;

        content: ": 2";
      }
    }

    &-2 {
      &:after {
        color: $blue;

        content: ": 3";
      }
    }

    &-3 {
      &:after {
        color: $blue;

        content: ": 1";
      }
    }

    @media only screen and (max-width: 900px) {
      max-width: 210px;
      font-size: 25px !important;
    }
    @media only screen and (max-width: 650px) {
      text-align: center;
      font-size: 20px;
      margin-left: 0;
    }
  }
  &:last-child {
    margin-bottom: 60px;
  }
  @media only screen and (max-width: 650px) {
    flex-direction: column;
  }
}

.hidden {
  display: none;
}

.nicescroll-cursors {
  left: 50px;
}

.search-solo {
  margin-top: 0 !important;
}

.logoImg {
  max-width: 36px;
}
