@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";

* {
  font-family: Lato;
}

body {
  // background: $silverbg;
  // -ms-overflow-style: none;
  /* Internet Explorer 10+ */
  //scrollbar-width: none;
  /* Firefox */
  width: auto;

  height: auto;
  background-image: linear-gradient(
    180deg,
    rgb(247, 247, 247) 50%,
    rgb(231, 229, 229) 100%
  );

  overflow-x: hidden;
  position: relative;
}

body::-webkit-scrollbar {
  display: none;
  /* Safari and Chrome */
}

html {
  overflow: auto;
  overflow-x: hidden;
}
::-webkit-scrollbar {
  // width: 0px; /* Remove scrollbar space */
  //background: transparent; /* Optional: just make scrollbar invisible */
}
/* Optional: show position indicator in red */
::-webkit-scrollbar-thumb {
  // background: #ff000000;
}

.slider {
  .slide {
    position: relative;
    .text {
      position: absolute;
      top: 25%;
      left: 50%;
      transform: translate(-50%, -50%);
      .innerText {
        position: absolute;
        display: flex;
        flex-direction: column;
        text-align: center;
        top: 50%;
        left: 50%;
        margin-top: 50px;
        transform: translate(-50%, -50%);
        .title {
          font-size: 70px;
          font-weight: lighter;
          color: $black;
          max-width: 75%;
          margin-left: 45px;
          @media only screen and (min-width: 750px) {
            margin-left: 10px;
            max-width: 90%;
          }
        }
        .subtitle {
          font-size: 36px;
          color: $black;
          font-weight: lighter;
          max-width: 75%;
          margin-left: 45px;
          @media only screen and (min-width: 750px) {
            margin-left: 10px;
            max-width: 90%;
          }
        }
      }
      .bgText {
        color: lighten($color: $silver, $amount: 5);
        font-size: 19vw !important;
        font-weight: bold;
      }
    }
    &_img {
      width: 100%;
      height: 100vh;
      object-fit: cover;
    }
    @media only screen and (max-width: 1500px) {
      .text {
        .innerText {
          .title {
            font-size: 50px;
          }
          .subtitle {
            font-size: 25px;
            color: $black;
            font-weight: lighter;
            width: 500px;
          }
          .btn {
            background: $blue;
            max-width: 220px;
            padding: 20px 50px;
            margin-top: 30px;
            border-radius: 50px;
            cursor: pointer;
            font-size: 18px;
            color: $white;
            display: flex;
            justify-content: center;
            align-self: center;
            box-shadow: 1px 1px 10px darken($color: $silver, $amount: 5);
          }
        }
        .bgText {
          color: lighten($color: $silver, $amount: 5);
          font-size: 150px;
          font-weight: bold;
        }
      }
    }
    @media only screen and (max-width: 750px) {
      .text {
        .innerText {
          .title {
            font-size: 40px;
          }
          .subtitle {
            font-size: 18px;
            color: $black;
            font-weight: lighter;
            width: 300px;
          }
          .btn {
            background: $blue;
            max-width: 220px;
            padding: 15px 30px;
            margin-top: 30px;
            border-radius: 50px;
            cursor: pointer;
            font-size: 16px;
            color: $white;
            display: flex;
            justify-content: center;
            align-self: center;
            box-shadow: 1px 1px 10px darken($color: $silver, $amount: 5);
          }
        }
        .bgText {
          color: lighten($color: $silver, $amount: 5);
          font-size: 60px;
          font-weight: bold;
        }
      }
    }
  }
}

.btn {
  background: $blue;
  max-width: 220px;
  padding: 20px 50px;
  margin-top: 30px;
  border-radius: 50px;
  cursor: pointer;
  font-size: 18px;
  color: $white !important;
  display: flex;
  justify-content: center;
  align-self: center;
  transition: all 0.3s ease;
  box-shadow: 1px 1px 10px darken($color: $silver, $amount: 5);
  &:hover {
    background: darken($color: $blue, $amount: 10) !important;
  }
  &-s {
    @media only screen and (min-width: 750px) {
      margin-right: 25px;
    }
    @media only screen and (min-width: 1500px) {
      margin-right: 50px !important;
    }
  }
  &-buy {
    align-self: center;
    margin: 40px auto;
    text-align: center;
    position: relative;
    text-decoration: none;
    font-size: 22px;
    color: white !important;
    width: 50vw;
    @media only screen and (max-width: 800px) {
      left: -5%;
      font-size: 20px;
      padding: 20px 50px !important;
    }
    @media only screen and (max-width: 650px) {
      width: 50vw;
      left: 0;
      margin: 40px 100px 10px;
    }

    // transform: translate(-50%, -50%);
  }
}

//DOTS
.slider {
  .slick-dots {
    position: absolute;
    top: 88%;
    left: 77%;
    max-width: 190px;
    max-height: 50px;
    //background: black;
    z-index: 1;
    @media only screen and (max-width: 600px) {
      display: none !important;
    }
    li {
      position: relative;
      margin-right: 15px;
      z-index: 1;
      .fa-circle:before {
        font-size: 14px;
        color: darken($color: $silver, $amount: 15);
        z-index: 1;
        @media only screen and (max-width: 600px) {
          display: none;
        }
      }
    }
    .slick-active {
      .fa-circle:before {
        color: $blue;
        background: $blue;
        border-radius: 50%;
        //margin-left: 20px;
      }
    }
  }
  @media only screen and (max-width: 900px) {
    .slick-dots {
      left: 65%;
    }
  }
}

.burger {
  background: $white;
  text-align: center;
  z-index: 1001;
  margin-left: 300px;
  padding: 30px;
  border-radius: 50%;
  box-shadow: 1px 1px 10px darken($color: $silver, $amount: 5);
  position: fixed;
  top: 10%;
  left: 80%;
  transform: translate(-50%, -50%);
  cursor: pointer;
  i {
    color: $blue;
  }
}

a {
  text-decoration: none;
}

a:visited {
  color: rgb(36, 36, 36);
}

.pushbarRight {
  .pushbarClose {
    background: $white;
    text-align: center;
    z-index: 999;
    border: none;
    padding: 30px;
    border-radius: 50%;
    box-shadow: 1px 1px 10px darken($color: $silver, $amount: 5);
    margin-top: 55px;
    margin-left: 139px;
    display: block;
    i {
      color: $blue;
    }
    cursor: pointer;
  }
}

div.pushbar_overlay {
  background: rgb(54, 54, 59);
}

.store {
  width: 100%;
  margin: 0 auto 50px;
  max-width: 1615px;
  .inner {
    margin-left: 87px;
    position: relative;
    @media only screen and (max-width: 950px) {
      margin-left: 90px;
    }
    .text {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 90px 15px 0px;
      @media only screen and (max-width: 955px) {
        margin: 90px 50px 50px;
      }
      @media only screen and (max-width: 767px) {
        align-items: flex-start;
        margin-left: 10px;
        margin-bottom: 30px !important;
        margin-right: 65px;
      }
      @media only screen and (max-width: 570px) {
        flex-direction: column;
        margin-top: 50px;
      }
      .store__title {
        font-size: 72px;
        font-weight: 100;
        @media only screen and (max-width: 900px) {
          font-size: 42px;
          text-align: start !important;
        }
        @media only screen and (max-width: 768px) {
          padding-left: 10px;
        }
        @media only screen and (max-width: 600px) {
          font-size: 32px;
          padding-left: 0px;
        }
      }
      .store__icon {
        color: $silver;
        display: flex;
        align-items: center;
        &_text {
          margin-right: 25px;
        }
        @media only screen and (max-width: 955px) {
          padding-right: 25px;
        }
        @media only screen and (max-width: 768px) {
          padding-right: 0px;
        }
        @media only screen and (max-width: 650px) {
          display: none;
        }
      }
    }
    .store__items {
      display: flex;
      @include row-flex();
      @include md(justify-content, center);
      max-width: 100%;
      margin-left: 0;
      margin-top: 0 !important;
      @media only screen and (max-width: 955px) {
        //  margin-left: 10px;
        margin-right: 30px;
      }
      @media only screen and (max-width: 768px) {
        //  margin-left: 10px;
        margin-right: 0px;
        margin-left: 10px;
      }
      @media only screen and (max-width: 600px) {
        //  margin-left: 10px;
        margin-right: 0px;
        margin-left: 0px;
        //  margin-left: 10px;
      }
      &_item {
        background: $white;
        box-shadow: 1px 1px 5px $silver;
        cursor: pointer;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-top: 30px;
        padding: 15px;
        transition: all 0.3s ease;
        &:hover .img {
          transform: scale(1.1);
        }
        &:hover .text {
          transform: scale(1.05);
        }
        &:hover {
          border-radius: 15px;
          transform: scale(1.015);
        }
        @media only screen and (max-width: 600px) {
          @include size(12);
          margin-left: 0;
        }
        &-solo {
          width: 90vw !important;
          height: 70vh !important;
          position: relative;
          top: 0;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          @media only screen and (max-width: 800px) {
            flex-direction: column;
            width: auto;
            margin-right: 50px;
          }
          @media only screen and (max-width: 600px) {
            margin-right: 35px;
            @include size(12);
          }
          .img {
            position: relative;
            margin-top: 0 !important;
            width: 100% !important;
            transform: scale(1.5);
            margin: 0 !important;
            left: -10% !important;
            @media only screen and (max-width: 1000px) {
              transform: scale(1.2) !important;
            }
            @media only screen and (max-width: 800px) {
              transform: scale(1.1) !important;
              margin: 0 !important;
              left: 0 !important;
            }
            @media only screen and (max-width: 600px) {
              margin-bottom: 40px !important;
            }
          }
          &:hover .img {
            transform: scale(1.6) !important;
            @media only screen and (max-width: 1000px) {
              transform: scale(1.3) !important;
            }
            @media only screen and (max-width: 800px) {
              transform: scale(1.2) !important;
            }
          }
          .text {
            justify-content: center;
            margin: 0 !important;
            max-width: 400px;
          }
        }
        .img {
          transition: all 0.5s ease;
          object-fit: scale-down;
          max-height: 420px;
          margin-bottom: 30px;
          max-width: 400px;
          margin: auto;
          @media only screen and (max-width: 1250px) {
            margin-bottom: 25px !important;
          }
          @media only screen and (max-width: 600px) {
            max-width: 200px;
          }
          @media only screen and (max-width: 400px) {
            max-width: 150px;
          }
        }
        .text {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          text-align: center;
          margin: 0 auto 30px;
          z-index: 2;
          transition: all 0.5s ease;
          .title {
            font-size: 30px;
            font-weight: lighter;
            margin-bottom: 5px;
            @media only screen and (max-width: 500px) {
              font-size: 18px;
            }
          }
          .desc {
            font-size: 18px;
            font-weight: lighter;
            text-align: start;
            color: darken($color: $silver, $amount: 8);
            margin-bottom: 15px;
            @media only screen and (max-width: 500px) {
              font-size: 15px;
            }
          }
          .price {
            font-size: 18px;
            font-weight: bold;
            color: $blue;
            @media only screen and (max-width: 500px) {
              font-size: 16px;
            }
          }
        }
      }
      &_item-1 {
        @include col();
        @include size(3);
        @media only screen and (max-width: 1250px) {
          @include size(4);
        }
        @media only screen and (max-width: 955px) {
          @include size(11);
        }
        .img {
          margin-bottom: 15px;
          max-width: 200px;
        }
      }
      &_item-2 {
        @include col();
        @include size(6);
        flex-direction: row;
        justify-content: center;
        @media only screen and (max-width: 1600px) {
          flex-direction: column;
        }
        @media only screen and (max-width: 1250px) {
          @include size(8);
        }
        @media only screen and (max-width: 955px) {
          @include size(11);
        }
        .img {
          margin-left: auto;
          max-width: 350px;
          @media only screen and (max-width: 1600px) {
            margin-right: auto;
            margin-bottom: 0;
          }
          @media only screen and (min-width: 1600px) {
            margin-left: 20px;
          }
        }
        .text {
          margin: auto;
          @media only screen and (max-width: 1600px) {
            margin-bottom: 40px;
          }
          .title {
            // font-size: 47px;
          }
          .desc {
            // font-size: 28px;
          }
          .price {
            //   font-size: 28px;
          }
        }
      }
      &_item-3 {
        @include col();
        @include size(3);
        @media only screen and (max-width: 1500px) {
          //  @include size(12);
        }
        @media only screen and (max-width: 1250px) {
          @include size(4);
        }
        @media only screen and (max-width: 955px) {
          @include size(11);
        }
        .text {
          text-align: center;
          margin: 0 auto 30px;
        }
        .img {
          margin-bottom: 15px;
        }
      }
      &_item-4 {
        @include col();
        @include size(9);
        flex-direction: row;
        justify-content: center;
        @media only screen and (max-width: 1250px) {
          @include size(8);
          flex-direction: column;
        }
        @media only screen and (max-width: 955px) {
          @include size(11);
        }
        .img {
          margin-left: auto;
          width: auto;
          @media only screen and (max-width: 1250px) {
            margin-right: auto;
          }
          @media only screen and (min-width: 1250px) {
            margin-left: 20px;
          }
        }
        .text {
          margin: auto;
          @media only screen and (max-width: 1250px) {
            margin-bottom: 40px;
          }
        }
      }
      &_item-5 {
        @include col();
        @include size(3);
        @media only screen and (max-width: 1250px) {
          @include size(12);
        }
        @media only screen and (max-width: 955px) {
          @include size(11);
        }
        .text {
          justify-content: center;
          text-align: center;
          margin: 0 auto 30px;
        }
        .img {
          margin-bottom: 15px;
        }
      }
    }
  }
}

