input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */

input[type="number"] {
  -moz-appearance: textfield;
}

.slider-single {
  width: 40%;
  // z-index: 3;
  @media only screen and (max-height: 900px) {
    width: 100%;
    height: 80vh;
  }
  @media only screen and (max-width: 1300px) {
    width: 100%;
    height: 80vh;
  }
  .slick-list {
    .slick-track {
      // display: flex;

      // // overflow-x: hidden;
      // // max-width: 100vw;
      // width: 100vw !important;
      // max-width: 100vw;
      .slick-slide {
        box-shadow: 1px 1px 15px 0px rgb(163, 163, 163);

        div {
          width: 100%;
          height: 100vh;
          background-color: #ffffff;
          display: flex;
          align-items: center;
          padding: 0 50px;
          @media only screen and (max-height: 900px) {
            align-items: flex-start;
            height: 80vh;
          }
          @media only screen and (max-width: 1300px) {
            align-items: flex-start;
            height: 80vh;
          }
        }
      }
    }
    .slick-track .slick-slide {
      display: flex;
      height: auto;
    }
  }
  .slick-dots {
    position: absolute;
    bottom: 5%;
    left: 10%;
    max-width: 290px;
    max-height: 50px;
    //background: black;
    z-index: 1;
    display: flex;
    width: 100%;
    flex-direction: row;
    @media only screen and (max-height: 900px) {
      bottom: 10%;
      left: 15%;
    }
    @media only screen and (max-width: 1300px) {
      bottom: 10%;
      left: 15%;
    }
    @media only screen and (max-width: 600px) {
      display: none !important;
    }
    li {
      position: relative;
      margin-right: 15px;
      z-index: 1;
      cursor: pointer;
      .fa-circle:before {
        font-size: 14px;
        color: darken($color: $silver, $amount: 15);
        z-index: 1;
        @media only screen and (max-width: 600px) {
          display: none;
        }
      }
    }
    .slick-active {
      .fa-circle:before {
        color: $blue;
        background: $blue;
        border-radius: 50%;
        //margin-left: 20px;
      }
    }
  }
  @media only screen and (max-width: 900px) {
    .slick-dots {
      // left: 65%;
    }
  }
}

li {
  list-style: none;
}

picture {
  display: flex;
